import Vue from 'vue';
import Router from 'vue-router';
import { routers } from './router';
const whiteList = ['/commodityClass', '/appcenter', '/login'];
Vue.use(Router);
const router = new Router({
    mode: 'history',
    base: '/mt',
    routes: routers
});
router.beforeEach((to, form, next) => {
    console.log(to, 'to===========');
    // let igNoreArr = ['paySuccess', 'orderConfirm', 'orderList', 'orderAdmin', 'commodityClass']
    // if (igNoreArr.indexOf(to.name) > -1) {
    //   next()
    // }
    // if (to.query.code || store.state.sessionId || ((to.name === 'login' || to.name === 'staffLogin') && to.query.openid)) {
    //   next()
    // }
    if (whiteList.indexOf(to.path) !== -1) {
        // in the free login whitelist, go directly
        next();
    }
    else {
        // other pages that do not have permission to access are redirected to the login page.
        next(`/login?redirect=${to.path}`);
    }
    setTitle(to.meta.title);
});
export function setTitle(title) {
    title = title || '';
    window.document.title = title;
}
export default router;
